  CARD
\* ˍˍˍˍˍˍˍˍˍ */
.fr-card {
  display: flex;
  flex-direction: column-reverse;
  position: relative;
  --text-spacing: 0 0 1rem;
  --title-spacing: 0 0 0.5rem;
}

.fr-grid-row .fr-card {
  height: 100%;
}

.fr-card:not(.fr-card--no-arrow) .fr-card__body::after {
  font: normal 700 normal 1.5rem/1 dsfr-icon;
  content: "";
  font-weight: 700;
  width: 1.5rem;
  height: 1.5rem;
  margin-top: auto;
  margin-left: auto;
}

.fr-card.fr-enlarge-link:hover .fr-card__img {
  --brightness: calc(100% + var(--brighten) * 10%);
}

.fr-card.fr-enlarge-link:active .fr-card__img {
  --brightness: calc(100% + var(--brighten) * 20%);
}

.fr-card__img {
  position: relative;
  width: 100%;
  filter: brightness(var(--brightness));
}

.fr-card__img::before {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 56.25%;
}

.fr-card__img img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

.fr-card__body {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  padding: 1.5rem;
}

.fr-card__detail {
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  order: -1;
}

.fr-card__title {
  font-weight:  700 ;
  font-size:  1.125rem ;
  line-height:  1.5rem ;
  font-weight: 700;
}

@media (min-width: 48em) {
  .fr-card__title {
    font-size:  1.25rem ;
    line-height:  1.75rem ;
  }
}

.fr-card__desc {
  font-size:  0.875rem ;
  line-height:  1.5rem ;
}

.fr-card__link {
  width: 100%;
  margin-top: auto;
}

@media (min-width: 48em) {
  .fr-card--horizontal {
    flex-direction: row-reverse;
  }
  .fr-card--horizontal .fr-card__img {
    max-width: 41.666%;
    flex: 1 0 auto;
  }
}

.fr-card {
  background-color: var(--background-default-grey);
  --hover: var(--background-default-grey-hover);
  --active: var(--background-default-grey-active);
  box-shadow: 0 0 0 1px var(--border-default-grey);
}

.fr-card--grey {
  background-color: var(--background-contrast-grey);
  --hover: var(--background-contrast-grey-hover);
  --active: var(--background-contrast-grey-active);
}

.fr-card:not(.fr-card--no-arrow) .fr-card__body::after {
  color: var(--text-action-high-blue-france);
}

.fr-card__detail {
  color: var(--text-mention-grey);
}

.fr-card__title {
  color: var(--text-title-grey);
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  /**
  * Correctif de la hauteur de la navigation latérale
  */
  .fr-card__title {
    margin: 0;
  }
  .fr-card--horizontal .fr-card__body {
    flex-basis: 100%;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-card {
    background-color: #fff;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-card:hover {
    background-color: #f6f6f6;
  }
  .fr-card:active {
    background-color: #ededed;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-card {
    box-shadow: 0 0 0 1px #e5e5e5;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-card--grey {
    background-color: #eee;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-card--grey:hover {
    background-color: #d2d2d2;
  }
  .fr-card--grey:active {
    background-color: #c1c1c1;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-card:not(.fr-card--no-arrow) .fr-card__body::after {
    color: #000091;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-card__detail {
    color: #666;
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .fr-card__title {
    color: #161616;
  }
}
